.PageContainer {
    margin: 1rem;
}
.ManageItemWrapper {
    /* height: 80vh;
    width: 80vw; */
    margin: 1rem;
}
.FormWrapper {
    -ms-flex-pack: center;
        justify-content: center;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -ms-flex-direction: row-reverse;
        flex-direction: row-reverse;
    margin: auto;
    /* border: 1px solid #ddd;
    box-shadow: 3px 5px #ddd; */
}
.FormSection {
    padding: 1%;
    margin: 1%;
    display: inline-block;
    width: 100%;
    /* height: 40vh; */
    text-align: center;
    
}
.FormContent {
    margin: 2rem;
}
.FormImgSection {
    padding: 1%;
    margin: 1%;
    display: inline-block;
    width: 100%;
    /* border: 1px solid blueviolet; */
    height: 70vh;
}
.FormImg {
    /* width: 100%; */
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    margin: auto;
}
.FormElement {
    width: 90%;
    margin: 5%;
    font-size: 16px;
}
input.FormElement {
    border: 1px solid #80808000;
    border-bottom: 1px solid gray;
    height: 1%;
}
/* 
  ##Device = Desktops
  ##Screen = 1281px to higher resolution desktops
*/

@media (min-width: 1281px) {
   
}


/* 
    ##Device = Laptops, Desktops
    ##Screen = B/w 1025px to 1280px
  */

@media (min-width: 1025px) and (max-width: 1280px) {
    
}


/* 
    ##Device = Tablets, Ipads (portrait)
    ##Screen = B/w 768px to 1024px
  */

@media (min-width: 768px) and (max-width: 1024px) {
    .FormWrapper {
        display: block;
    }
    .FormImgSection {
        padding: 0%;
        height: 50vh;
    }
}


/* 
    ##Device = Tablets, Ipads (landscape)
    ##Screen = B/w 768px to 1024px
  */

@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {
    .FormWrapper {
        display: -ms-flexbox;
        display: flex;
    }
    .FormImgSection {
        padding: 1%;
        height: 70vh;
    }
}


/* 
    ##Device = Low Resolution Tablets, Mobiles (Landscape)
    ##Screen = B/w 481px to 767px
  */

@media (min-width: 481px) and (max-width: 767px) {
    .FormWrapper {
        display: block;
    }
    .FormImgSection {
        padding: 0%;
        height: 50vh;
    }
}


/* 
    ##Device = Most of the Smartphones Mobiles (Portrait)
    ##Screen = B/w 320px to 479px
  */

@media (min-width: 320px) and (max-width: 480px) {
    .FormWrapper {
        display: block;
    }
    .FormImgSection {
        padding: 0%;
        height: 50vh;
    }
}

.ItemDetail {
    margin-top: 2rem;
}
.ItemDetailBrand {
    font-size: 1.8rem;
    font-weight: 700;
}
.ItemDetailPrice {
    font-size: 1.5rem;
    font-weight: 500;
}
.ItemDetailDesc {
    font-size: 1.3rem;
    font-weight: 300;
}


