/* Product CSS :: START */
.FormImg {
    height: 450px;
    /* width: 100%; */
}
.Products {
    width: 100%;
}
.Product {
    width: 23%;
    display: inline-block;
    text-align: center;
    /* border: 1px solid #000;
    box-shadow: 3px 5px grey; */
    margin: 0.6rem;
}
.ProductButtonWrapper {
    text-align: center;
}
.ProductButton {
    font-size: 20px;
    font-weight: bold;
    background: #0a0a0a;
    color: #fff;
    border: 1px solid black;
    -webkit-box-shadow: 2px 3px;
            box-shadow: 2px 3px;
    border-radius: 8px;
    cursor: pointer;
    padding-left:20px;
    padding-right:20px;
}
.ProductButton:hover {
    background: rgba(0, 0, 0, 0.60);
}
.ProductImg {
    text-align: center;
}
.ProductPriceInfo {
    width: 100%;
    margin-top: 10px;
    text-align: center;
    display: inline-block;
}
.PriceSuffix {
    color: gray;
    font-size: 14px;
    width: 100%;
    text-align: center;
}
.product-info {
    display: block;
    text-align: left;
    margin: 0.5rem;
}
.ProductInfoLine1 {
    width: 100%;

}
.ProductInfoLine1Left {
    display: inline-block;
    width: 85%;
}
.ProductInfoLine1Right {
    display: inline-block;
    width: 15%;
}
@media (max-width: 850px) {
    .Product {
        width: 30%;
    }
}
@media (max-width: 650px) {
    .Product {
        width: 45%;
    }
}
@media (max-width: 450px) {
    .Product {
        width: 93%;
    }
}
/* Product CSS :: END */

/* CART CSS :: START */
.SidecartWrapper {
    /* Sticking side cart to right */
    position: fixed;
    top: 69px;
    right: 0;
    bottom: 0;
    /* Transparent shadow with dark depth fading off */
    -webkit-box-shadow: 0 0 2em rgba(0, 0, 0, .7);
            box-shadow: 0 0 2em rgba(0, 0, 0, .7);
    /* Cart background */
    background-color: #fff;
    /* Cart width */
    width: 35%;
}

.SidecartContainer {
    /* Container to align cart items vertically */
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    /* height 100% is important to make cart-items section scrollable */
    height: 100%;
}

.CartHeader {
    font-size: 1.5rem;
    padding: 1rem;
    border-bottom: 1px solid #ddd;
    /* To Show Cart on left and item count on right */
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.CartItems {
    /* flex: auto; Absorbs any extra space along the main axis */
    -ms-flex: auto;
        flex: auto;
    /* Brings scroll bar */
    overflow-x: hidden;
    overflow-y: auto;
    scroll-behavior: smooth;
}

.CartItemWrapper {
    padding: 1rem;
    border-bottom: 1px solid #ddd;
}

.CartItem {
    display: -ms-flexbox;
    display: flex;
}

.CartItemImgWrapper {
    position: relative;
    margin-right: 1rem;
}

.CartItemQuantityPrice {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    margin-top: 1rem;
}

.DecrementBtn {
    border-top-left-radius: 24px;
    border-bottom-left-radius: 24px;
    padding: 10px;
}

.IncrementBtn {
    border-top-right-radius: 24px;
    border-bottom-right-radius: 24px;
    padding: 10px;
}

.DecrementBtn:focus,
.IncrementBtn:focus {
    outline: none;
}

.QuantityDisplay {
    padding: 10px;
    width: 10px;
}

.QuantityPicker {
    display: -ms-flexbox;
    display: flex;
}

.CartItemImg {
    width: 70px;
    height: 70px;
}

.CartItemDetail {
    font-size: 1.0rem;
    width: 100%;
}

.CartFooter {
    padding: 1rem;
    border-top: 1px solid #ddd;
    text-align: center;
}

.CartTotalSection {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
}

.TotalLabel {
    font-size: 1.1rem;
}

.TotalPrice {
    padding-left: 0.5rem;
    font-size: 1.2rem;
    font-weight: 700;
}

.CartEmptyMsg {
    padding: 12rem 1rem;
    text-align: center;
}

.ButtonPrimary {
    background-color: #0065ff;
    color: white;
    border-radius: 15px;
    padding: 5px 20px;
    font-size: 21px;
}


/* 
  ##Device = Desktops
  ##Screen = 1281px to higher resolution desktops
*/

@media (min-width: 1281px) {
    .SidecartWrapper {
        width: 30%;
    }
}


/* 
    ##Device = Laptops, Desktops
    ##Screen = B/w 1025px to 1280px
  */

@media (min-width: 1025px) and (max-width: 1280px) {
    .sidecart-wrapper {
        width: 30%;
    }
}


/* 
    ##Device = Tablets, Ipads (portrait)
    ##Screen = B/w 768px to 1024px
  */

@media (min-width: 768px) and (max-width: 1024px) {
    .sidecart-wrapper {
        width: 50%;
    }
}


/* 
    ##Device = Tablets, Ipads (landscape)
    ##Screen = B/w 768px to 1024px
  */

@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {
    .sidecart-wrapper {
        width: 50%;
    }
}


/* 
    ##Device = Low Resolution Tablets, Mobiles (Landscape)
    ##Screen = B/w 481px to 767px
  */

@media (min-width: 481px) and (max-width: 767px) {
    .sidecart-wrapper {
        width: 100%;
    }
}


/* 
    ##Device = Most of the Smartphones Mobiles (Portrait)
    ##Screen = B/w 320px to 479px
  */

@media (min-width: 320px) and (max-width: 480px) {
    .sidecart-wrapper {
        width: 100%;
    }
}
/* CART CSS :: END  */


